.project {
	max-width: 300px;
	border-radius: 8px;
	text-align: center;
	padding: 20px;
	border: 1px solid transparent;
	background-color: hsla(0, 0%, 99.6%, .84);
	box-shadow: 4px 4px 32px #c0c0c0;
	-webkit-transition: .5s ease;
	transition: .5s ease;
	list-style: none;
}

.project__title {
	font-family: 'Montserrat', Arial, sans-serif;
	font-style: normal;
	font-weight: 700;
	font-size: 1.5rem;
	margin-top: 0;
	background-image: linear-gradient(135deg, #68a59c, #afc2bd);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

.project__description {
	font-family: 'Montserrat', Arial, sans-serif;
	margin-bottom: 70px;
	font-style: normal;
	font-weight: 500;
	font-size: 1.1rem;
}

.project__link-conteiner {
	display: grid;
	grid-template-columns: 1fr 1fr;
	column-gap: 20px
}

.project__image {
	max-width: 250px;
	max-height: 250px;
	cursor: pointer;
}

.project__link {
	font-family: 'Montserrat', Arial, sans-serif;
	font-size: 1.2rem;
	color: #87a69f;
	border: 2px solid transparent;
	border-image: linear-gradient(135deg, #68a59c, white);
	border-image-slice: 1;
	position: relative;
	font-weight: 700;
	line-height: 1;
	z-index: 1;
	transition: all .6s cubic-bezier(.19, 1, .22, 1);
	text-decoration: none;
	height: 25px;
	text-align: center;
	padding: 10px 30px 10px 30px;
}

.project__link:hover {
	color: white;
	background-image: linear-gradient(135deg, #68a59c, #afc2bd);
}

@media screen and (max-width: 980px) {
	.project__link {
		padding: 5px 15px 5px 15px;
	}
}

.project__link:nth-of-type(2n) {
	margin-right: 0px;
}
